@import url("https://fonts.googleapis.com/css2?family=Almarai:wght@700;800&display=swap");

.attr {
  width: 100%;
  min-height: 100vh;
  padding: 1em;
  padding-bottom: 200px;
  background-color: #473129;
  background-image: url("./pattern-mobile.svg");
  background-repeat: no-repeat;
  background-position: bottom 20% right;
  background-size: 150px;
  color: #c6ac9f;
  font-family: Almarai, sans-serif;
  font-size: 30px;

  &__link {
    display: block;
    width: max-content;
    text-decoration: none;
    color: inherit;
    -webkit-text-fill-color: #c6ac9f;
    -webkit-text-stroke: #c6ac9f;
    -webkit-text-stroke-width: 0;
    font-size: 2em;
    line-height: 1.2em;
    margin-bottom: 0.4em;

    transition: 200ms ease;

    &:hover {
      -webkit-text-fill-color: #473129;
      -webkit-text-stroke-width: 2px;
      text-decoration: underline;
      text-decoration-thickness: 3px;
    }
  }

  &__hr {
    width: 100%;
    color: inherit;
    margin-bottom: 1em;

    transition: 200ms ease;
  }

  &__btns {
    display: flex;
    gap: 0.4em;

    a {
      text-decoration: none;
      text-align: center;
      font-size: 0.8em;
      background-color: #c6ac9f;
      color: #473129;
      border: none;
      padding: 0.3em;
      border-radius: 99em;
      cursor: pointer;

      transition: background-color 200ms ease;

      &:hover {
        background-color: #473129;
        outline: 2px solid #c6ac9f;
        color: #c6ac9f;
      }
    }
  }

  &__para {
    transition: 200ms ease;
  }
}

@media screen and (min-width: 900px) {
  .attr {
    font-size: 40px;
    background-image: url("./pattern-desktop.svg");
    background-position: top right 10%;
    background-size: 90px;
    padding-bottom: 1em;

    &__hr {
      width: 10em;
    }
  }
}
