:root{font-family:"Kumbh Sans",Arial,sans-serif;font-weight:400;font-size:16px}*{margin:0;padding:0;box-sizing:border-box}.App{min-height:100vh}
:root{font-family:"Kumbh Sans",Arial,sans-serif;font-weight:400;font-size:16px}.Navigation{position:fixed;inset:0;z-index:1}.Navigation::before{content:"";position:fixed;inset:0;background-color:rgba(0,0,0,.75);z-index:-1;-webkit-animation:fade-in 300ms;animation:fade-in 300ms}.Navigation__wrapper{height:100%;width:15.5em;background-color:#fff;-webkit-animation:slide-in 300ms;animation:slide-in 300ms;padding:1.5em}.Navigation__close-btn{-webkit-appearance:none;appearance:none;border:none;background-color:transparent;display:flex;align-items:center;margin-bottom:4em;cursor:pointer;outline-offset:2px;outline-color:#1d2025}.Navigation__list{list-style-type:none;display:flex;flex-direction:column;grid-gap:1.4em;gap:1.4em;font-size:1.1rem;font-weight:700}.Navigation__link{display:block;text-decoration:none;color:#1d2025;position:relative;outline:none;overflow:hidden}.Navigation__link span{display:block;transition:-webkit-transform 50ms;transition:transform 50ms;transition:transform 50ms, -webkit-transform 50ms}.Navigation__link::after{content:"";position:absolute;inset-block:0;left:0;width:0;background-color:#ff7d1a;border-radius:0 99em 99em 0;transition:width 50ms}.Navigation__link:focus span,.Navigation__link:hover span{-webkit-transform:translateX(20px);transform:translateX(20px)}.Navigation__link:focus::after,.Navigation__link:hover::after{width:4px}.slide-out{-webkit-animation:slide-out 300ms forwards;animation:slide-out 300ms forwards}.fade-out::before{-webkit-animation:fade-out 300ms forwards;animation:fade-out 300ms forwards}@-webkit-keyframes slide-in{from{-webkit-transform:translateX(-100%);transform:translateX(-100%)}to{-webkit-transform:none;transform:none}}@keyframes slide-in{from{-webkit-transform:translateX(-100%);transform:translateX(-100%)}to{-webkit-transform:none;transform:none}}@-webkit-keyframes slide-out{from{-webkit-transform:none;transform:none}to{-webkit-transform:translateX(-100%);transform:translateX(-100%)}}@keyframes slide-out{from{-webkit-transform:none;transform:none}to{-webkit-transform:translateX(-100%);transform:translateX(-100%)}}@-webkit-keyframes fade-in{from{opacity:0}to{opacity:1}}@keyframes fade-in{from{opacity:0}to{opacity:1}}@-webkit-keyframes fade-out{from{opacity:1}to{opacity:0}}@keyframes fade-out{from{opacity:1}to{opacity:0}}
:root{font-family:"Kumbh Sans",Arial,sans-serif;font-weight:400;font-size:16px}.CartItem{display:flex;align-items:center;grid-gap:1em;gap:1em}.CartItem__thumbnail{width:3.1em;border-radius:.3em}.CartItem__info{color:#68707d;overflow:hidden}.CartItem__title{white-space:nowrap;overflow:hidden;text-overflow:ellipsis;font-weight:400;margin-bottom:.3em}.CartItem__total-value{color:#1d2025}.CartItem__delete-btn{margin-left:auto;-webkit-appearance:none;appearance:none;background-color:transparent;border:none;cursor:pointer}
:root{font-family:"Kumbh Sans",Arial,sans-serif;font-weight:400;font-size:16px}.Cart{position:absolute;top:calc(100% + .6em);inset-inline:.6em;background-color:#fff;border-radius:.8em;-webkit-filter:drop-shadow(0 10px 15px rgba(0, 0, 0, 0.3));filter:drop-shadow(0 10px 15px rgba(0, 0, 0, 0.3));-webkit-animation:fade-in 300ms;animation:fade-in 300ms}.Cart__heading{color:#1d2025;font-size:1rem;padding:1.6em 1.4em;border-bottom:1px solid #b6bcc8}.Cart__wrapper{min-height:180px;padding:1.6em 1.4em;display:flex;flex-direction:column;justify-content:center;grid-gap:1.6em;gap:1.6em}.Cart__empty-msg{align-self:center;font-weight:700;color:#68707d}.Cart__checkout-btn{-webkit-appearance:none;appearance:none;width:100%;padding:1.1em;border-radius:.5em;background-color:#ff7d1a;border:none;font:inherit;color:#f7f8fd;font-weight:700;cursor:pointer;box-shadow:0 0 #e66300;transition-property:box-shadow,-webkit-transform;transition-property:transform,box-shadow;transition-property:transform,box-shadow,-webkit-transform;transition-duration:200ms}.Cart__checkout-btn:hover,.Cart__checkout-btn:focus-visible{-webkit-transform:translateY(-5px);transform:translateY(-5px);box-shadow:0 5px #e66300}.Cart__checkout-btn:active{-webkit-transform:unset;transform:unset;box-shadow:0 0 #e66300}.Cart.fade-out{-webkit-animation:fade-out 300ms forwards;animation:fade-out 300ms forwards}@-webkit-keyframes fade-in{from{opacity:0}to{opacity:1}}@keyframes fade-in{from{opacity:0}to{opacity:1}}@-webkit-keyframes fade-out{from{opacity:1}to{opacity:0}}@keyframes fade-out{from{opacity:1}to{opacity:0}}
.Header{display:flex;align-items:center;grid-gap:1em;gap:1em;padding:1.2em 1.5em;position:relative}.Header__toggle-nav,.Header__toggle-cart{cursor:pointer;-webkit-appearance:none;appearance:none;background-color:transparent;border:none;display:inline-flex;align-items:center;outline-offset:2px}.Header__logo{-webkit-transform:translateY(-1px);transform:translateY(-1px)}.Header__toggle-cart{margin-left:auto;margin-right:.4em}.Header__avatar{box-sizing:border-box;height:calc(1.5em + 2px);border:solid 2px transparent;border-radius:99em;transition:border-color 100ms;cursor:pointer}.Header__avatar:hover{border-color:#ff7d1a}
.attr{width:100%;min-height:100vh;padding:1em;padding-bottom:200px;background-color:#473129;background-image:url(/frontend-mentor/ecommerce-product-page-app/static/media/pattern-mobile.ea10ceb0.svg);background-repeat:no-repeat;background-position:bottom 20% right;background-size:150px;color:#c6ac9f;font-family:Almarai,sans-serif;font-size:30px}.attr__link{display:block;width:-webkit-max-content;width:max-content;text-decoration:none;color:inherit;-webkit-text-fill-color:#c6ac9f;-webkit-text-stroke:#c6ac9f;-webkit-text-stroke-width:0;font-size:2em;line-height:1.2em;margin-bottom:.4em;transition:200ms ease}.attr__link:hover{-webkit-text-fill-color:#473129;-webkit-text-stroke-width:2px;text-decoration:underline;text-decoration-thickness:3px}.attr__hr{width:100%;color:inherit;margin-bottom:1em;transition:200ms ease}.attr__btns{display:flex;grid-gap:.4em;gap:.4em}.attr__btns a{text-decoration:none;text-align:center;font-size:.8em;background-color:#c6ac9f;color:#473129;border:none;padding:.3em;border-radius:99em;cursor:pointer;transition:background-color 200ms ease}.attr__btns a:hover{background-color:#473129;outline:2px solid #c6ac9f;color:#c6ac9f}.attr__para{transition:200ms ease}@media screen and (min-width: 900px){.attr{font-size:40px;background-image:url(/frontend-mentor/ecommerce-product-page-app/static/media/pattern-desktop.81479920.svg);background-position:top right 10%;background-size:90px;padding-bottom:1em}.attr__hr{width:10em}}
